@import './variables.scss';

.home {
  text-align: center;

  .name-input {
    @extend %labowlet-input;
    position: relative;
    border-radius: 10px;
    box-shadow: $subtle-shadow;
    top: 20vh;
    left: 0;
    width: 100%;
    font-size: 20px;
    font-family: $game-font;
    line-height: 30px;
    padding-bottom: 8px;
  }

  .button-group {
    position: relative;
    top: 40vh;

    .generic-button {
      margin:15px;
      border-radius: 10px;

    }

    .create-btn {
      transition: 300ms cubic-bezier(0.53,-0.49, 0.43, 1.62);
    }

    .join-btn {
      transition: 300ms cubic-bezier(0.53,-0.49, 0.43, 1.62) 100ms;
    }
  
    .create-btn.invisible {
      opacity: 0;
      bottom:-100px;
    }

    .join-btn.invisible {
      opacity: 0;
      bottom:-100px;
    }
  
    .create-btn.visible {
      bottom:0px;
      opacity: 1;
    }

    .join-btn.visible {
      bottom:0px;
      opacity: 1;
    }
  }
}
