@import 'variables';

.bowl {
  text-align: center;
  font-family: $game-font;

  .words-container {
    display: inline-block;

    .words {
      animation: slide-in-left 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
      display: flex;
      & > .remove-words-btn {
        @extend %clear-button;
        font-family: $primary-font;
        font-weight: bold;
        background: none;
        border: none;
        color: black;
      }
    }
  }
  .word-input {
    @extend %labowlet-input;
    border-radius: 10px;
    font-size: 20px;
    line-height: 30px;
    box-shadow: $subtle-shadow;
  }
  .word-submit {
    font-size: 20px;
    height: 40px;
    border-radius: 10px;
    box-shadow: $subtle-shadow;
    &.disabled-btn {
      opacity: 0.8;
      background-color: grey;
    }
  }

}

.page-footer {
  .foot-header {
    text-align: justify;
    margin-left: 10px;
    line-height: 20px;
    // font-size: 20px;
  }

  .foot-header > h3 {
    margin: .75em .75em .75em 0em;
  }
}

.add-word-options {
  justify-content: center;
  display: flex;
}

@keyframes slide-in-left{0%{transform:translateX(-1000px);opacity:0}100%{transform:translateX(0);opacity:1}}