@import './variables.scss';

.player-icon {
  background-color: $background-color-third;
  border-radius: 50px;

  /*
    Set horizontal line
  */
  display: flex;
  justify-content: center;
  align-items: center;
  float:left;

  width: 52px;
  height: 52px;
  margin-bottom: 10px;
  margin-right: 10px;
  margin-left: 10px;

  box-shadow: $font-color-dark 2px 3px 3px 0px;

  p {
    color: $background-color-primary;
    line-height: 10px;
  }
}